.follow-button {
  top: 10px;
  left: 16px;
  width: 90px;
  height: 30px;
  border: none;
  outline: none;
  color: chocolate;
  font-weight: 600;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.follow-button:before {
  content: '';
  background: -webkit-linear-gradient(45deg, #ff0000, #ff7300, #fffb00,
   #48ff00, #00ffd5, #021679, #7a00ff, #ff00c8, #ff0000);
  background: -o-linear-gradient(45deg, #ff0000, #ff7300, #fffb00,
   #48ff00, #00ffd5, #021679, #7a00ff, #ff00c8, #ff0000);
  background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00,
   #48ff00, #00ffd5, #021679, #7a00ff, #ff00c8, #ff0000);
  position: absolute;
  top: -2px;
  left:-2px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 0;
  -webkit-transition: opacity .3s ease-in-out;
  -o-transition: opacity .3s ease-in-out;
  transition: opacity .3s ease-in-out;
  border-radius: 10px;
}

.follow-button:active:after {
  background: transparent;
}

.follow-button:hover:before {
  opacity: 1;
}

.follow-button:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(243, 216, 180);
  left: 0;
  top: 0;
  border-radius: 10px;
}

@-webkit-keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}

@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}


